// Basic Library
import { VFC } from 'react';
import { Bar } from 'react-chartjs-2';
import { subDays } from 'date-fns';
import format from "date-fns/format";
import { convertDate, langSet } from "../config";

export type Props = {
  chartData: number[];
}

const BarChart: VFC <Props> = (Props) => {
  const { chartData } = Props;
  const today = new Date();
  let weekLabel: string[] = [];
  [...Array(7)].map((_, i) => {
    const prev: number = 7 - i;
    const targetDate = format(convertDate(subDays(today, prev)).dateOrigin, 'eee');
    weekLabel.push(String(targetDate));
    }
  )
  const data = {
    labels: weekLabel,
    datasets: [
      {
        label: langSet.common.chart.bar,
        data: chartData,
        backgroundColor: [
          'rgba(255, 99, 132, 0.2)',
          'rgba(54, 162, 235, 0.2)',
          'rgba(255, 206, 86, 0.2)',
          'rgba(75, 192, 192, 0.2)',
          'rgba(153, 102, 255, 0.2)',
          'rgba(255, 159, 64, 0.2)',
          'rgba(255, 99, 132, 0.2)',
        ],
        borderColor: [
          'rgba(255, 99, 132, 1)',
          'rgba(54, 162, 235, 1)',
          'rgba(255, 206, 86, 1)',
          'rgba(75, 192, 192, 1)',
          'rgba(153, 102, 255, 1)',
          'rgba(255, 159, 64, 1)',
          'rgba(255, 99, 132, 1)',
        ],
        borderWidth: 1,
      },
    ],
  };
  
  const options = {
    scales: {
      yAxes: [
        {
          ticks: {
            beginAtZero: true,
          },
        },
      ],
    },
  };
  return (
    <Bar data={data} />
  )
}

export default BarChart;